    @import url('https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&family=Offside&family=Source+Sans+3:ital,wght@0,200..900;1,200..900&display=swap');

    :root{
        --TextColor: #6EACDA;
        --TitleText: "Offside", serif;
        --MainText: "Source Sans 3", serif; 
    }
    html,body{
        height: 100%;
        width: 100%;
        margin: 0rem;
        padding: 0rem;
        overflow-x: hidden;
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;
        -webkit-text-size-adjust: 100%; 
        overflow-x: hidden;
    }
    body::before {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: -1;
        background-image: url("/src/components/Images/backround.jpg");
        background-size: cover;
        background-position: center;
        background-color: rgba(2,21,38,0.85);
        background-blend-mode: darken;
    }
    .home, .about, .skillset, .projects{
        display: flex;  
        flex-direction: column;
        justify-content: center; 
        align-items: center;
        min-height: 100dvh;
        width: 100%;               
        margin: 0 auto;
        text-align: center;
    }
    .section-title{
        color: var(--TextColor);
        font-family: var(--TitleText);
        font-size: 4rem;
        margin-bottom: 4rem;
        display: block;
    }
    /* HEADER SECTION */

    .header{
        position: fixed;
        z-index: 100;
        top:0;
        width: 100vw;
        height: 9vh;
        border-radius: 0 0 2rem 2rem;
        display: flex;
        align-content: center;
        align-items: center;
        background-color:rgba(2,21,38,.25);
    }
    .header a{
        text-decoration: none;
        color: var(--TextColor);
        margin:  .5rem 0 1rem 5rem ;
        font-size: 2.5rem;
        font-family: var(--TitleText);
    }
    .headerText{
        color: var(--TextColor);
        font-size: 3rem;
        font-family: "Nothing You Could Do", serif;
        display: inline-flex;
        margin:  .5rem 0 .5rem 2rem ;
        width: 48vw;
    }
    .navigation-container{
        display: inline-flex;
        justify-content: end;
        align-content: end;
        width: 48vw;
        padding: 0;
    }
    .mobile-container{
        display: none;
        justify-content: end;
        align-content: center;
        width: 48vw;
    }
    .mobile-icon{
        color: var(--TextColor);
        font-size: 3rem;
        margin:  .5rem 0 .5rem 2rem ;
        display: inline-block;
    }

    /* HOME SECTION */

    .home p{
        color: var(--TextColor);
        font-family: var(--MainText);
        font-size: 3rem;
        display: block;
    }
    .home a{
        color: var(--TextColor);
    }
    .home .name{
        font-size: 8rem;
    }
    .home .main-container{
        width: fit-content;
        justify-items: center;
        margin:  12rem 0;
    }
    .socials{
        background-color: rgba(110, 172, 218, .1);
        height: 8vh;
        width: 45rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10rem;
    }
    .socials a{
        display: flex;
        text-underline-offset: .5rem;
        margin: 1rem;
    }
    .socials a:hover{
        text-underline-offset: .8rem;
        transition: 200ms  ease-in-out;
    }
    .socials span{
        color: var(--TextColor);
        font-family: var(--MainText);
        font-size: 1.5rem;
    }
    .home p{
        color: var(--TextColor);
        font-family: var(--MainText);
        font-size: 2rem;
        margin: 1rem;
    }
    .socials img{
        aspect-ratio: 1;
        height: 2.3rem;
        padding: 0  .5rem;
    }

    /* ABOUT SECTION */

    .about .main-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 80vw;    
        margin: 0 auto;
    }
    .about img{
        height:20rem;
        aspect-ratio: 1;
        border: 1rem outset rgba(2,21,38,.1);
        display: block;
        margin: 3rem;
    }
    .about span{
        color: var(--TextColor);
        display: block;
        font-family: var(--MainText);
        font-size: 3rem;
        text-align:center;
    }

    /* SKILLSET SECTION */


    .skillset .main-container {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        gap: 2rem;
        margin-top: 8rem;
    }
    .skillset .skillTitle {
        color: var(--TextColor);
        font-family: var(--MainText);
        font-size: 3rem;
        white-space: nowrap;
        margin: 0;
        padding-top: 1rem;
    }
    .skillset .skills-list {
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
        flex: 1;
    }
    .skillset .skillItem {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 7rem;
    }
    .skillset img {
        display: block;
        aspect-ratio: 1/1;
        height: 4rem;
        margin-bottom: 0.5rem;
    }
    .skillset .skillName {
        color: var(--TextColor);
        font-family: var(--MainText);
        font-size: 1.5rem;
        text-align: center;
    }


    /* PROJECTS SECTION */
    .projects .main-container {
        width: 90vw;
        max-width: 1400px;
        display: flex;
        flex-direction: column;
    }
    .grid-container {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
        gap: 2rem;
        width: 100%;
    }
    .project-card {
        background-color: rgba(2, 21, 38, 0.7);
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 1rem;
        display: flex;
        flex-direction: row;
        height: 16rem;
        overflow: hidden;
        transition: transform 0.3s ease, box-shadow 0.3s ease;
    }
    .project-card:hover {
        transform: translateY(-5px);
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
        border-color: rgba(255, 255, 255, 0.2);
    }
    .image-container {
        width: 40%;
        height: 100%;
        display: flex;
        justify-content: center; 
        align-items: center;
        flex-shrink: 0;
    }
    .project-image {
        width: 90%;
        height: 90%;
        object-fit:contain;
        transition: transform 0.5s ease;
    }
    .project-card:hover .project-image {
        transform: scale(1.05);
    }
    .content-container {
        padding: 1.5rem;
        display: flex;
        flex-direction: column;
        width: 60%;
        color: var(--TextColor);
        align-items: flex-start;
        text-align: left;
    }
    .card-header {
       display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        
    }
    .project-title {
        font-family: var(--TitleText);
        font-size: 1.5rem;
        font-weight: 600;
        margin: 0;
        display: flex;
        align-items: center;
        gap: 0.5rem;
    }
    .external-link-icon {
        color: var(--TextColor);
        font-size: 1.5rem;
        transition: transform 0.2s;
        display: flex;
        align-items: center;
    }
    .external-link-icon:hover {
        transform: translate(2px, -2px);
        color: #6eabda;
    }
    .github-icon {
        font-size: 2rem;
        color: WHITE;
        opacity: 0.8;
        transition: opacity 0.5s;
    }
    .github-icon:hover {
        opacity: 1;
    }
    .project-description {
        font-family: var(--MainText);
        font-size: 1rem;
        line-height: 1.5;
        color: #94a3b8;
        flex-grow: 1;
        margin-bottom: 1rem;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
    }
    .tags-container {
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;
    }
    .tech-tag {
        font-size: 1rem;
        font-family: monospace;
        background-color: rgba(110, 171, 218, 0.15);
        color: var(--TextColor);
        padding: 0.25rem 0.75rem;
        border-radius: 2rem;
        font-weight: 500;
    }
    /* RESPONSIVE */
    @media only screen and (max-width:1200px){
        :root{
            font-size: 90%;
        }
    }
    @media only screen and (max-width:1000px){
        .header a{
            margin:  .5rem 0 1rem 2rem ;
        }
        .home .social-text{
            font-size: 3rem;
        }
        .socials{
            height: 8rem;
            width: 50rem;
        }
        .socials img{
            height: 3rem;
        }
        .socials span{
            font-size: 2rem;
        }
        .section-title{
            font-size: 5rem
        }
    }
    @media only screen and (max-width:1000px){
        :root{
            font-size: 80%;
        }
    }
    @media only screen and (max-width:900px){
        .mobile-container{
            display: inline-flex;
            height: inherit;
            align-items: center;
        }
        .headerText{
            font-size: 3rem;
            display: inline-block;
        }
        .header{
            align-content: center;
        }
        .about img{
            height: 25rem;
        }
        .about span{
            font-size: 2.5rem
        }
        .mobile-icon{
            z-index: 999;
            width: fit-content;
            height: fit-content;
            font-size: 5rem;
            margin: 0 2rem;
        }
        .skillset .main-container {
            flex-direction: column;
            gap: 1rem;
        }
        
        .skillset .skillTitle {
            flex: auto;
            width: 100%;
            text-align: center;
            margin-bottom: 1rem;
        }
        
        .skillset .skills-list {
            justify-content: center;
        }
        .navigation-container{
            transform: translateX(+100%);
            z-index: 99;
            top: 0;
            left: 0;
            margin: none;
            background-color:#021526;
            height: 100vh;
            width:100vw;
            position: absolute;
            display: grid;
            row-gap: 5rem;
            justify-content: center;
            align-content: center;
            text-align: center;
            transition: all .5s ease-in-out;
        }
        .active .navigation-container{
            transform: translateX(0);
            transition: all .5s ease-in-out;
        }
        .navigation-container a{
            font-size: 5rem;
        }
        .projects{
            height: fit-content;
            padding-bottom: 5rem;
        }
        .grid-container {
            grid-template-columns: 1fr;
            width: 100%;
        }
        .project-card{
            flex-direction: column;
            gap: 2rem;
            height: 45rem;
        }
        .image-container{
            width: 100%;
            height: 40%;
            margin-top: 2rem;
        }
        .project-image{
            height: 100%;
            width: 70%;
        }
        .content-container{
            width: 95%;
        }
        .github-icon{
            font-size: 3rem;
            margin: 0 1rem 0 0 ;
            opacity: 1;
            height: 2rem;
        }
        .project-title{
            font-size: 3rem;
        }
        .external-link-icon{
            font-size: 2.5rem;
        }
        .project-description{
            font-size: 1.8rem;
            margin-bottom: 1.5rem;
        }
        .tech-tag{
            font-size: 1.3rem;
            padding: .5rem 1rem;
        }
    }
    @media only screen and (max-width:800px){
        :root{
            font-size:70% 
        }
    }
    @media only screen and (max-width:700px){
        .socials{
            width: 50rem;
        }
        .socials span{
            color: var(--TextColor);
            font-family: var(--MainText);
            font-size: 2rem;
        }
        .socials img{
            height: 3rem;
        }
    }
    @media only screen and (max-width:650px){
        .home .name{
            font-size: 5.5rem
        }
        .skillset .main-container{
            grid-template-rows: repeat(4,1fr);
            grid-template-columns: repeat(2, 1fr);
        }
        .section-title{
            font-size: 5rem;
        }
        .mobile-container{
            width: 45vw;
        }
        .socials{
            width: 42rem;
        }
        .socials span{
            color: var(--TextColor);
            font-family: var(--MainText);
            font-size: 1.7rem;
        }
        .socials img{
            height: 2.5rem;
        }
        
    }
    @media only screen and (max-width:500px){
        :root{
            font-size: 50%;
        }
    }
    @media only screen and (max-width:350px){
        :root{
            font-size: 40%;
        }
    }